event.stopPropagation();\r\n                this.activeName = 'first';\r\n                this.showDetail = false;\r\n            },\r\n            handleSizeChange(val) {\r\n                this.pageSize = val;\r\n                this.search();\r\n            },\r\n            handleCurrentChange(val) {\r\n                this.currentPage = val;\r\n                this.search();\r\n            },\r\n            handleClick(tab, event) {\r\n                let index = tab.index;\r\n                if (index == 0) {\r\n                    this.activeName = 'first';\r\n                }\r\n            },\r\n            exportExcel() {\r\n            \tvar self = this;\r\n                require.ensure([], () => {\r\n                    const {export_json_to_excel} = require('vendor/Export2Excel');\r\n                    const tHeader = [\r\n                    \t'日期',\r\n                        '渠道',\r\n                        '设备',\r\n                        '注册',\r\n                        '基本信息/注册',\r\n                        '借款申请/注册',\r\n                        '联系人/注册',\r\n                        '身份证/注册',\r\n                        '芝麻/注册',\r\n                        '银行卡/注册',\r\n                        '运营商/注册',\r\n                        '淘宝/注册',\r\n                        '支付宝/注册',\r\n                        '基本信息',\r\n                        '借款申请',\r\n                        '联系人',\r\n                        '身份证',\r\n                        '芝麻',\r\n                        '银行卡',\r\n                        '运营商',\r\n                        '淘宝',\r\n                        '支付宝',];\r\n                    const filterVal = [\r\n                    \t'reg_date',\r\n                        'type',\r\n                        'platform',\r\n                        'reg_num',\r\n                        'base_info_rate',\r\n                        'loan_info_rate',\r\n                        'link_info_rate',\r\n                        'pid_info_rate',\r\n                        'zhima_info_rate',\r\n                        'bank_info_rate',\r\n                        'phone_info_rate',\r\n                        'taobao_info_rate',\r\n                        'zhifubao_info_rate',\r\n                        'base_info',\r\n                        'loan_info',\r\n                        'link_info',\r\n                        'pid_info',\r\n                        'zhima_info',\r\n                        'bank_info',\r\n                        'phone_info',\r\n                        'taobao_info',\r\n                        'zhifubao_info',];\r\n                    const list = self.tableData;\r\n                    const data = this.formatJson(filterVal, list);\r\n                    export_json_to_excel(tHeader, data, '认证转化报表');\r\n                })\r\n            },\r\n            formatJson(filterVal, jsonData) {\r\n                return jsonData.map(v => filterVal.map(j => {\r\n                    let returnValue;\r\n                    if (j == 'operator') {\r\n                        returnValue = v[j].name;\r\n                    } else {\r\n                        returnValue = v[j];\r\n                    }\r\n                    return returnValue;\r\n                }))\r\n            },\r\n\r\n        },\r\n        created(){\r\n\r\n        },\r\n        mounted(){\r\n            this.getDevices();\r\n            this.search();\r\n        }\r\n    }\r\n</script>\r\n"]}]);
