<template>
    <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="运营报表" name="first">
            <div class="p_6">
                <div class="search-wrapper">
                    <div class="search-content">
                        <el-select v-model="searchParam.type" clearable slot="prepend" placeholder="渠道" style="width: 120px;">
                            <el-option
                                v-for="item in typeOptions"
                                :key="item.value"
                                :label="item.name"
                                :value="item.value">
                            </el-option>
                        </el-select>
                        <el-select v-model="searchParam.device" slot="prepend" placeholder="手机设备" style="width: 120px;">
                            <el-option
                                v-for="item in deviceOptions"
                                :key="item.value"
                                :label="item.name"
                                :value="item.value">
                            </el-option>
                        </el-select>
                        <el-date-picker
                            v-model="searchParam.dateRange"
                            type="daterange"
                            placeholder="选择日期范围">
                        </el-date-picker>
                    </div>
                    <div class="search-btn">
                        <el-button slot="append" icon="search" @click="search" style="width:80px; ">查询</el-button>
                    </div>

                </div>
                <el-button class="right mb-10" type="primary" size="small" icon="el-icon-document"
                           @click="exportExcel">导出excel
                </el-button>
                <el-table

                    :data="tableData" v-loading.body="listLoading" element-loading-text="拼命加载中" style="width: 100%" border
                    :default-sort="{prop: 'time', order: 'descending'}">
                    <el-table-column
                        type="index"
                        align="center"
                        width="50">
                    </el-table-column>
                    <el-table-column prop="reg_date" label="日期" align="center" width="115" sortable></el-table-column>
                    <el-table-column prop="type" label="渠道" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="platform" label="设备" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="reg_num" label="注册" align="center" width="80" sortable></el-table-column>

                    <el-table-column prop="base_info_rate" label="基本信息/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="loan_info_rate" label="借款申请/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="link_info_rate" label="联系人/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="pid_info_rate" label="身份证/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="zhima_info_rate" label="芝麻/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="bank_info_rate" label="银行卡/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="phone_info_rate" label="运营商/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="taobao_info_rate" label="淘宝/注册" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="zhifubao_info_rate" label="支付宝/注册" align="center" width="80" sortable></el-table-column>

                    <el-table-column prop="base_info" label="基本信息" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="loan_info" label="借款申请" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="link_info" label="联系人" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="pid_info" label="身份证" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="zhima_info" label="芝麻" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="bank_info" label="银行卡" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="phone_info" label="运营商" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="taobao_info" label="淘宝" align="center" width="80" sortable></el-table-column>
                    <el-table-column prop="zhifubao_info" label="支付宝" align="center" width="80" sortable></el-table-column>

                </el-table>
                <div class="pagination_position">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="pageSizes"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>
                </div>
            </div>
        </el-tab-pane>
        <div v-if="showDetail">
            <el-tab-pane name="second">
                <span slot="label"> 详情页<i class="el-icon-close" @click="removeLabel($event)"></i></span>
            </el-tab-pane>
        </div>
    </el-tabs>
</template>

<style scoped>
    .el-select .el-input {
        width: 100px;
    }

    date-range:after {
        display: block;
        content: '.';
        line-height: 1;
        clear: both;
        visibility: hidden;
    }

    .pagination_position {
        padding-top: 20px;
    }

    .echarts {
        height: 300px;
    }
    .right{
        float: right;
    }
    .mb-10 {
        margin-bottom: 10px;
    }
</style>
<style>
    .el-table .cell, .el-table th > div {
        padding-left: 5px;
        padding-right: 5px;
        box-sizing: border-box;
        text-overflow: ellipsis;
    }
</style>
<script>
    import Common from '../../../utils/common'
    import {Notification} from 'element-ui';
    import formatDate from '../../../utils/formatDate.js'
    export default{
        data(){
            let date = new Date();
            let prevMonth = new Date(date.setDate(date.getDate() - 30));
            let now = new Date();

            return {
                deviceOptions: [
                    {
                        value: 'All',
                        name: 'All',
                    }, {
                        value: 'iOS',
                        name: 'iOS',
                    }, {
                        value: 'Android',
                        name: 'Android'
                    }
                ],
                typeOptions: [
                    {
                        value: 'ALL',
                        name: 'ALL',
                    }, {
                        value: '市场厂商',
                        name: '市场厂商',
                    }, {
                        value: 'H5渠道',
                        name: 'H5渠道'
                    }
                ],
                detailData: {},
                searchParam: {
                    device: "All",
                    dateRange: [prevMonth, now],
                    time: '',
                    type: '',
                },
                activeName: 'first',
                showDetail: false,
                name: 'detail',
                total: 0,
                currentPage: 1,
                pageSizes: [10, 20, 50, 100, 200],
                pageSize: 50,
                tableData: [],
                select: "",
                listLoading: true,
            }
        },
        methods: {
            getDevices(){
                Common.ajax({
                    url: '/web/v1/options/devices',
                    type: 'POST',
                    contentType: 'application/json',
                    success: (response) => {
                        if (response.data && response.data.list) {
                            this.deviceOptions = response.data.list;
                        }
                    },
                    error: (err) => {
                        this.deviceOptions = [{
                            value: 'All',
                            name: 'All',
                        }, {
                            value: 'iOS',
                            name: 'iOS',
                        }, {
                            value: 'Android',
                            name: 'Android'
                        }];
                    }
                });
            },
            search(){
                let self = this;
                let dateRange = self.searchParam.dateRange;
                this.listLoading = true;
                Common.ajax({
                    url: "/web/v1/operation/authProcessDay",
                    type: 'POST',
                    contentType: "application/json",
                    data: {
                    	type: self.searchParam.type,
                        page: self.currentPage,
                        pageSize: self.pageSize,
                        device: self.searchParam.device,
                        beginTime: formatDate(dateRange[0], 'yyyy-MM-dd') + ' 00:00:00',
                        endTime: formatDate(dateRange[1], 'yyyy-MM-dd') + ' 23:59:59',
                    },
                    success(response){
                        self.listLoading = false;
                        if (response.data && response.data.list) {
                            let data = response.data;
                            self.tableData = data.list;
                            self.total = data.pageUtil.count;
                        }
                    },
                    error(err){
                        self.listLoading = false;
                        let error = err.message || err;
                        Notification.error({
                            title: '错误',
                            message: error
                        });
                    }
                })
            },
            removeLabel(event){
                event.stopPropagation();
                this.activeName = 'first';
                this.showDetail = false;
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.search();
            },
            handleClick(tab, event) {
                let index = tab.index;
                if (index == 0) {
                    this.activeName = 'first';
                }
            },
            exportExcel() {
            	var self = this;
                require.ensure([], () => {
                    const {export_json_to_excel} = require('vendor/Export2Excel');
                    const tHeader = [
                    	'日期',
                        '渠道',
                        '设备',
                        '注册',
                        '基本信息/注册',
                        '借款申请/注册',
                        '联系人/注册',
                        '身份证/注册',
                        '芝麻/注册',
                        '银行卡/注册',
                        '运营商/注册',
                        '淘宝/注册',
                        '支付宝/注册',
                        '基本信息',
                        '借款申请',
                        '联系人',
                        '身份证',
                        '芝麻',
                        '银行卡',
                        '运营商',
                        '淘宝',
                        '支付宝',];
                    const filterVal = [
                    	'reg_date',
                        'type',
                        'platform',
                        'reg_num',
                        'base_info_rate',
                        'loan_info_rate',
                        'link_info_rate',
                        'pid_info_rate',
                        'zhima_info_rate',
                        'bank_info_rate',
                        'phone_info_rate',
                        'taobao_info_rate',
                        'zhifubao_info_rate',
                        'base_info',
                        'loan_info',
                        'link_info',
                        'pid_info',
                        'zhima_info',
                        'bank_info',
                        'phone_info',
                        'taobao_info',
                        'zhifubao_info',];
                    const list = self.tableData;
                    const data = this.formatJson(filterVal, list);
                    export_json_to_excel(tHeader, data, '认证转化报表');
                })
            },
            formatJson(filterVal, jsonData) {
                return jsonData.map(v => filterVal.map(j => {
                    let returnValue;
                    if (j == 'operator') {
                        returnValue = v[j].name;
                    } else {
                        returnValue = v[j];
                    }
                    return returnValue;
                }))
            },

        },
        created(){

        },
        mounted(){
            this.getDevices();
            this.search();
        }
    }
</script>
